import React, { Fragment } from "react"
import render from "react-dom"
import { Link } from "gatsby"
import Helmet from "react-helmet"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"

import "tabler-react/dist/Tabler.css";

import { Card, Button, Page } from "tabler-react";
import { Map, TileLayer, Marker, Popup } from "react-leaflet";

const MyPopupMarker = ({ content, position }) => (
  <Marker position={position}>
    <Popup>{content}</Popup>
  </Marker>
)

const MyMarkersList = ({ markers }) => {
  const items = markers.map(({ key, ...props }) => (
    <MyPopupMarker key={key} {...props} />
  ))
  return <Fragment>{items}</Fragment>
}


class GOPCmap extends React.Component {
  
  fetchGOPCMap(){    
    var _this = this;
    
      axios
        .get(`https://raw.githubusercontent.com/OurHealthcareHK/api/master/GOPCINFO.json`)
        .then(({ data }) => {
          
          if (!data)
            return;     
          
          var markers = [];
          
          for (var i = 1; i < data.length; i++){
            var lat = data[i][9],
              long = data[i][10],
          	  nameTC = data[i][4],
          	  addressTC = data[i][5],
          	  bookingTC = data[i][15],
          	  consultTC = data[i][16];
              
              var userLat = "";
              var userLng = "";
            
            markers.push({ key:lat, position:[lat,long], content: '<p><strong>'+nameTC+'</strong><br>'+
		addressTC+'<br><a href="https://citymapper.com/directions?startcoord='+userLat+','+userLng+'&startname=你現在的位置&endcoord='+lat+','+long+'&endname='+nameTC+'&endaddress='+addressTC+'" target="_blank" rel="noopener noreferrer">如何前往？</a></p><br><p><strong>診症時間：</strong>'+consultTC+'<br><strong>登記時間：</strong>'+bookingTC+'</p>'  });
              
          }
          
          _this.state.markers = markers;
          console.log(markers);
          _this.setState(_this.state);
          
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
  }
  
  constructor() {
    super()
    this.state = {
      lat: 22.38269281766774,
      lng: 114.10987863448963,
      zoom: 10,
      markers: [
            { key: 'marker1', position: ["22.445600775", "114.02316813"], content: 'My first popup' },
            { key: 'marker2', position: ["22.382476673", "114.20150951"], content: 'My second popup' },
            { key: 'marker3', position: ["22.360806128", "114.12539813"], content: 'My third popup' },
          ]
    }
  }
  
	componentDidMount() {
    this.fetchGOPCMap();
	}

	render() {
    const position = [this.state.lat, this.state.lng];
    if (typeof window !== 'undefined') {
      return (
      <Layout>
    <SEO title="普通科診所地圖" description="醫院管理局的普通科門診資料，包括地址、電話、診症時間等。"/>
    <Page.Content title="普通科診所地圖">
      <Helmet>
        <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/leaflet/1.3.1/leaflet.css"/>
        <script src="https://unpkg.com/leaflet@1.4.0/dist/leaflet.js"
          integrity="sha512-QVftwZFqvtRNi0ZyCtsznlKSWOStnDORoefr1enyq5mVL4tmKB3S/EnC3rRJcxCPavG10IcrVGSmPh6Qw5lwrg=="
          crossorigin=""></script>
      </Helmet>
        <Map style={{ width: "100%", height: 400 }} center={ [this.state.lat, this.state.lng] } zoom={this.state.zoom}>
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <MyMarkersList markers={this.state.markers} />
      </Map>
    </Page.Content>
      </Layout>
      );
    }
    return null;
  }
}

export default GOPCmap
